.navBar {
    height: 60px;
    display: flex;
    flex-direction: row;
}

.toolBar {
    display: flex;
    justify-content: space-between;
}

.containerNavMenu {
    flex: 1;
    display: none;
}

.containerNavItems {
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: row;
}

.navItem {
    height: 100%;
    width: 150px;
    font-family: Inter, serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
}

.containerUserMenu {
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

